.lightmode {
	// custom variables light mode
	--background-color: #f7edf0;
	--nav-background-color: #50282f;
	--nav-acrylic: #50282fea;
	--nav-links: #ffffff;
	--nav-hover: #ff857e;
	--text: #011627;
	--text-highlight: #e63958;
	--text-footer: #ffffff;
	--colormode-outer: #ffffff;
	--colormode-inner: #df6980;
	--colormode-hover: #e798a763;
	--colormode-hover-inner: #ffa3b5;
	--colormode-hover-outer: #ffffff;
	--scroll: #e6395981;
	--hamburger: #ffffff;
	--stripe1: #f4afab;
	--stripe2: #ff857e;
	--stripe3: var(--stripe1);
	--stripe4: var(--stripe2);
	--stripe5: var(--stripe1);
	--stripe6: var(--stripe1);
	--deco-color1: #f9d7d3;
	--work-color: #f4cbc6;
	--tools-color: #011627a9;
	--link-color: #011627;
	--skills-color: #ff857e;
	--filter: brightness(0);
	--text-light: #ffffff;
	--button-color: #ffffff;
	--text-button-color: #ffffff;
	--hover-bg: #e63958;
	--hover-text: #ffffff;
}

.darkmode {
	// custom variables dark mode
	--background-color: #1e1919;
	--nav-background-color: #42212b;
	--nav-acrylic: #42212bbb;
	--nav-links: #ffffff;
	--nav-hover: #f3607b;
	--text: #ffffff;
	--text-highlight: #f3607b;
	--text-footer: #ffffff;
	--colormode-inner: #ffffff;
	--colormode-outer: #df6980;
	--colormode-hover: #e798a763;
	--colormode-hover-outer: #ffa3b5;
	--colormode-hover-inner: #ffffff;
	--scroll: #e6395981;
	--hamburger: #ffffff;
	--stripe1: #df6980;
	--stripe2: #c04654;
	--stripe3: var(--stripe1);
	--stripe4: var(--stripe2);
	--stripe5: var(---stripe1);
	--stripe6: var(--stripe1);
	--deco-color1: #30292a;
	--work-color: #342729;
	--tools-color: #edededa1;
	--link-color: #ffffff;
	--skills-color: #c04654;
	--filter: brightness(1);
	--button-color: #f3607b;
	--text-button-color: #ffffff;
	--hover-bg: #f3607b;
	--hover-text: #ffffff;
}
